import React, { useState, useEffect } from 'react';
import { SiFacebook, SiLinkedin } from 'react-icons/si';
import { BsPerson, BsBook, BsFileEarmarkText, BsHouse } from 'react-icons/bs';
import { AiOutlineMenu } from 'react-icons/ai';
import { FiArrowUpCircle, FiX } from 'react-icons/fi';
import { IconContext } from 'react-icons';
import { Link, animateScroll as scroll } from 'react-scroll';
import Fade from 'react-reveal/Fade';
import { ReactComponent as Logo } from '../assests/logo.svg';

const Header = (props) => {
  const { isMobileNav, handleMobileNav } = props;
  const [isScrollToTop, setIsScrollToTop] = useState(false);

  const handleScrollToTop = () => {
    scroll.scrollToTop({ duration: 800 });
  };

  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      if (currentScrollY > 100) {
        setIsScrollToTop(true);
      } else {
        setIsScrollToTop(false);
      }
    };
    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => window.removeEventListener('scroll', handleScroll);
  }, [isScrollToTop]);

  return (
    <IconContext.Provider value={{ className: 'react-icons' }}>
      <header id="header" style={{ left: isMobileNav && 0 }}>
        <div className="d-flex flex-column">
          <div className="profile">
            <h1 className="text-light">
              <a href="index.html">Daniel Ilan</a>
            </h1>
            <div className="social-links mt-3 text-center">
              <a href="https://www.facebook.com/DDaniel772" className="facebook">
                <SiFacebook />
              </a>
              <a href="https://www.linkedin.com/in/daniel-ilan" className="linkedin">
                <SiLinkedin />
              </a>
            </div>
          </div>

          <nav className="nav-menu">
            <ul>
              <li>
                <Link activeClass="active" to="hero" spy={true} smooth={true} duration={500} offset={-50}>
                  <BsHouse /> <span>Home</span>
                </Link>
              </li>
              <li>
                <Link to="about" spy={true} smooth={true} duration={500} offset={-50}>
                  <BsPerson /> About
                </Link>
              </li>
              <li>
                <Link to="portfolio" spy={true} smooth={true} duration={500} offset={-50}>
                  <BsBook /> Portfolio
                </Link>
              </li>
              <li>
                <Link to="resume" spy={true} smooth={true} duration={500} offset={-50}>
                  <BsFileEarmarkText /> Resume
                </Link>
              </li>
            </ul>
          </nav>

          <Logo className="logo" />

          <button type="button" className="mobile-nav-toggle d-xl-none" onClick={handleMobileNav}>
            {isMobileNav ? <FiX /> : <AiOutlineMenu />}
          </button>
          <Fade bottom opposite when={isScrollToTop} duration={500}>
            <button type="button" className="back-to-top" onClick={handleScrollToTop}>
              <FiArrowUpCircle />
            </button>
          </Fade>
        </div>
      </header>
    </IconContext.Provider>
  );
};

export default Header;
