import React from 'react';
import styles from './achivments.module.css';
import { BsAward } from 'react-icons/bs';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Fade from 'react-reveal/Fade';

const Achivments = () => {
  return (
    <section id="achivments" className="achivments content-box">
      <Container>
        <div className="section-title">
          <h2>
            Achivments
            <span className={styles.headerIcon}>
              <BsAward />
            </span>
          </h2>
        </div>
        <Row>
          <Col lg={6} className="pt-4 pt-lg-0 content">
            <Fade left delay={250}>
              <div className="pb-4 px-4">
                <h4>Excellence award – Final project </h4>
                <p>
                  As the final project of my academic studies, I developed, together with Rubi Deri, a web interface
                  that would lower video post-production costs and time required. The project won an excellence award in
                  the name of the late Prof. Miki Ronen and HIT. The project is referred as “The most technologically
                  complex ever to be produced in the faculty”.
                </p>
              </div>
            </Fade>
          </Col>
          <Col lg={6} className="pt-4 pt-lg-0 content">
            <Fade left delay={250}>
              <div className="pb-4 px-4">
                <h4>Published academic paper </h4>
                <p>
                  For my seminar work in my studies, I researched learners viewing patterns and dropout rate in a MOOC
                  with an innovative approach using bigdata analysis. The paper was published in Chais convention and
                  was nominated to “Best student paper” of that year.
                </p>
                <a
                  className={styles.link}
                  href="https://www.openu.ac.il/Lists/MediaServer_Documents/innovation/chais/2020/b2_2.pdf"
                >
                  Link to paper
                </a>
              </div>
            </Fade>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default Achivments;
