import React, { useEffect, useRef, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import Carousel from 'react-bootstrap/Carousel';
import Fade from 'react-reveal/Fade';
import { IoIosArrowDropdown } from 'react-icons/io';
import styles from './portfolio.module.css';

function PortfolioItem(props) {
  const { project, index } = props;
  const [showText, setShowText] = useState(false);
  const [shouldGrow, setShouldGrow] = useState(false);
  const cardBodyRef = useRef();

  const handleShowText = () => {
    return setShowText(!showText);
  };

  useEffect(() => {
    setShouldGrow(cardBodyRef.current.clientHeight >= 142);
  }, []);

  return (
    <Col lg={4} md={6} className={styles.portfolioItem}>
      <div className={styles.portfolioIndex}>{index + 1}</div>
      <Fade left delay={index * 250}>
        <Card className={styles.customCard}>
          <Carousel controls={false} fade={true} pause={false} interval={null} className={styles.customCarusel}>
            {project.images.map((image, index) => {
              return (
                <Carousel.Item key={index} className={styles.caruselItem}>
                  <img className={styles.caruselImage} src={image} alt="First slide" />
                </Carousel.Item>
              );
            })}
          </Carousel>
          <Card.Body
            ref={cardBodyRef}
            className={`${styles.cardText} ${showText ? styles.cardGrowAnimation : styles.cardShrinkAnimation}`}
          >
            <Card.Title>{project.title}</Card.Title>
            <Card.Text>{project.description}</Card.Text>
            {shouldGrow && (
              <div className={styles.textShadow}>
                <IoIosArrowDropdown
                  onClick={handleShowText}
                  size="2em"
                  className={`${showText ? styles.rotateDown : styles.rotateUp} ${styles.downIcon}`}
                />{' '}
              </div>
            )}
          </Card.Body>
          <Card.Body>
            {project.link && (
              <Button variant="outline-primary" href={project.link}>
                {' '}
                Go to project
              </Button>
            )}
          </Card.Body>
          <Card.Footer className={styles.cardFooter}>
            {project.icons.map((Icon, index) => {
              return <Icon key={index}></Icon>;
            })}
          </Card.Footer>
        </Card>
      </Fade>
    </Col>
  );
}

export default PortfolioItem;
